import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllOrders, updateOrder, fetchOrderById, deleteOrderById } from './adminAPI';

const initialState = {
  status: 'idle',
  orders: null,
  totalOrders: null,
};

export const fetchAllOrdersAsync = createAsyncThunk(
  'admin/fetchAllOrders',
  async (ordersPagination) => {
    const response = await fetchAllOrders(ordersPagination);
    return response;
  }
);
 
export const updateOrderAsync = createAsyncThunk(
  'admin/updateOrder',
  async ({orderId, status}) => {
    const response = await updateOrder({orderId, status})
    return response;
  }
)
 
export const deleteOrderByIdAsync = createAsyncThunk(
  'admin/deleteOrderById',
  async ({orderId}) => {
      const response = await deleteOrderById(orderId)
      return response
    }
)
 
export const adminSlice = createSlice({
  name: 'admin',
  initialState,
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAllOrdersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllOrdersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.orders = action.payload.ordersData;
        state.totalOrders = action.payload.totalOrders;
      })
      .addCase(updateOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        let updatedOrderIndex = state.orders.findIndex(el=>el.id === action.payload.id)
        state.orders[updatedOrderIndex] = action.payload
      })
      .addCase(deleteOrderByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteOrderByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
          let filteredOrders = state.orders.filter(el=>el.id !== action.payload)
          state.orders = filteredOrders
        }
      )
  },
});

export const { increment } = adminSlice.actions;

export const selectAdminOrders = (state) => state.admin.orders;
export const selectAdminOrdersStatus = (state) => state.admin.status;
export const selectAdminTotalOrders = (state) => state.admin.totalOrders;

export default adminSlice.reducer;
