import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteOrderByIdAsync, fetchAllOrdersAsync, selectAdminOrders, selectAdminOrdersStatus, selectAdminTotalOrders, updateOrderAsync } from "../adminSlice";
import { EyeIcon, PencilIcon, TrashIcon } from "@heroicons/react/24/outline";
import {ChevronLeftIcon, ChevronRightIcon } from "@heroicons/react/24/outline";
import WarnModal from "../../comman/WarnModal";
import Loader from '../../comman/Loader'

const AdminOrders = () => {
  const RESULT_PER_PAGE = 10
  const [open, setOpen] = useState(false)
  const [itemId, setItemId] = useState(null);
  const [currentItem, setCurrentItem] = useState({})
  const [page, setPage] = useState(1)
  const [ordersPagination, setOrdersPagination] = useState({page:page, limit:RESULT_PER_PAGE});
  const dispatch = useDispatch();
  const orders = useSelector(selectAdminOrders);
  const selectTotalOrders = useSelector(selectAdminTotalOrders)
  const adminOrdersStatus = useSelector(selectAdminOrdersStatus)
  const [totalOrders, setTotalOrders] = useState(selectTotalOrders)
  const [editOrder, setEditOrder] = useState({orderId:-1});

  const chooseColor = (status) => {
    switch (status) {
      case "Pending":
        return "bg-yellow-200 text-black";
      case "Dispatched":
        return "bg-yellow-800 text-white";
      case "Shipped":
        return "bg-lime-200 text-black";
      case "Delivered":
        return "bg-lime-600 text-white";
      case "Cancelled":
        return "bg-red-600 text-white";
      default:
        return "";
    }
  };

  const handleStatus = (orderId, e) => {
    dispatch(updateOrderAsync({orderId, status:e.target.value}))
    setEditOrder({orderId:-1})
  };

  const handleDeleteOrder = ({orderId, data}) => {
    setOpen(true)
    setCurrentItem(data.item)
    setItemId({orderId})
   }

  const handleDeleteConfirm = () => {
    setTotalOrders(totalOrders-1)
    dispatch(deleteOrderByIdAsync(itemId));
    setItemId(null)
  }

  const handlePrev = () => {
    if(page>1){
      setOrdersPagination({page:page-1, limit:RESULT_PER_PAGE})
      setPage(page-1)
    }
  }
  
  const handleNext = () => {
    if(page<(totalOrders/RESULT_PER_PAGE)){
      setOrdersPagination({page:page+1, limit:RESULT_PER_PAGE})
      setPage(page+1)
    }
  }

  const handlePage = (selectedPage) => {
    setOrdersPagination({page:selectedPage, limit:RESULT_PER_PAGE})
    setPage(selectedPage)
  }

  useEffect(() => {
    dispatch(fetchAllOrdersAsync(ordersPagination));
  }, [dispatch, ordersPagination]);

  useEffect(()=>{
    setTotalOrders(selectTotalOrders)
  }, [selectTotalOrders])

  return (
    <div className="overflow-x-auto">
      <WarnModal open={open} setOpen={setOpen} handleDeleteConfirm={handleDeleteConfirm} currentItem={currentItem}/>
      { adminOrdersStatus === "loading"? <Loader/> : <div className="flex items-center justify-center bg-gray-100 font-sans overflow-hidden">
        <div className="w-full lg:w-5/6">
          <div className="bg-white shadow-md rounded my-6 flex justify-center ">
            <table className="table-auto w-full">
              <thead>
                <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                  <th className="py-3 px-6 text-left">No.</th>
                  <th className="py-3 px-6 text-left">Order Id</th>
                  <th className="py-3 px-6 text-left">Product</th>
                  <th className="py-3 px-6 text-center">Address</th>
                  <th className="py-3 px-6 text-center">Payment</th>
                  <th className="py-3 px-6 text-center">Status</th>
                  <th className="py-3 px-6 text-center">Actions</th>
                </tr>
              </thead>
              <tbody className="text-gray-600 text-sm font-light">
                {orders?.map((data, index) =>
                <tr key={data.id} className="border-b border-gray-200 hover:bg-gray-100">
                      <td className="py-3 px-6 text-left whitespace-nowrap">
                        <div className="flex items-center">
                          <span className="font-medium">
                            <strong>{((page-1)*RESULT_PER_PAGE) + (index+1)}</strong>
                          </span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-left whitespace-nowrap">
                        <div className="flex items-center">
                          <span className="font-medium">
                            <strong>#{data.id}</strong>
                          </span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-left">
                        <div className="flex items-center">
                          <div className="mr-2">
                            <img
                              className="w-10 h-10 rounded-lg"
                              src={data.item.thumbnail}
                            />
                          </div>
                          <div className="font-medium">
                            <span>
                              <strong>{data.item.title}</strong>
                            </span>
                            <br />
                            <span>
                              <strong>{data.item.description}</strong>
                            </span>
                            <br />
                            <span>
                              <strong>Quantity: {data.item.quantity}</strong>
                            </span>
                            <br />
                            <span>
                              <strong>
                                Price: $
                                {Math.round(
                                  data.item.price *
                                    (1 - data.item.discountPercentage / 100)
                                ) * data.item.quantity}
                              </strong>
                            </span>
                          </div>
                        </div>
                      </td>
                      <td className="py-3 px-6">
                        <div className="font-medium">
                          <span>
                            <strong>{data.address.name}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.email}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.street}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.city}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.state}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.postalCode}</strong>
                          </span>
                          <br />
                          <span>
                            <strong>{data.address.telephone}</strong>
                          </span>
                          <br />
                        </div>
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="font-medium">
                          <span>
                            <strong>{data.paymentMethod}</strong>
                          </span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-center">
                        {editOrder.orderId === data.id ? (
                          <select
                            onChange={(e) => handleStatus(data.id, e)}
                          >
                            <option value="Pending">--Select Status--</option>
                            <option value="Pending">Pending</option>
                            <option value="Dispatched">Dispatched</option>
                            <option value="Shipped">Shipped</option>
                            <option value="Delivered">Delivered</option>
                            <option value="Cancelled">Cancelled</option>
                          </select>
                        ) : (
                          <span
                            className={`${chooseColor(
                              data.status
                            )} py-1 px-3 rounded-full text-xs`}
                          >
                            {data.status}
                          </span>
                        )}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="flex item-center justify-center">
                          <div className="w-6 mr-2 transform hover:text-yellow-500 hover:scale-110">
                            <EyeIcon style={{ cursor: "pointer" }} />
                          </div>
                          <div className="w-6 mr-2 transform hover:text-blue-500 hover:scale-110">
                            <PencilIcon style={{ cursor: "pointer"}} onClick={()=>setEditOrder({orderId:data.id})}/>
                          </div>
                          <div className="w-6 mr-2 transform hover:text-red-500 hover:scale-110">
                            <TrashIcon style={{ cursor: "pointer" }} onClick={()=>handleDeleteOrder({orderId:data.id, data})} />
                          </div>
                        </div>
                      </td>
                    </tr>
                )}
              </tbody>
            </table>
          </div>
          {/* // Pagination section */}
          <Pagination page={page} totalItems={totalOrders} RESULT_PER_PAGE={RESULT_PER_PAGE} handleNext={handleNext} handlePrev={handlePrev} handlePage={handlePage} />
        </div>
      </div>}
    </div>
  );
};

function Pagination({handlePage, page, totalItems, RESULT_PER_PAGE, handlePrev, handleNext}) {
  return (
    <div className="flex items-center my-6 shadow-md rounded justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <button
          onClick={e=>handlePrev()}
          disabled={page===1}
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </button>
        <button
          onClick={e=>handleNext()}
          disabled={page*RESULT_PER_PAGE>=totalItems}
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </button>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing <span className="font-medium">{(page*RESULT_PER_PAGE-RESULT_PER_PAGE)+1}</span> to{" "}
            <span className="font-medium">{totalItems>15? page*RESULT_PER_PAGE > totalItems? totalItems : page*RESULT_PER_PAGE : totalItems}</span> of{" "}
            <span className="font-medium">{totalItems}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
            <button
              onClick={e=>handlePrev()}
              disabled={page===1}
              className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </button>
            {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}
            {Array.from({length:Math.ceil(totalItems/RESULT_PER_PAGE)}).map((el, index)=>
              <div
              style={{cursor: 'pointer'}}
              key={index}
              onClick={e=>handlePage(index+1)}
              aria-current="page"
              className={`relative z-10 inline-flex items-center ${page===index+1? 'bg-indigo-600 text-white' : '' } px-4 py-2 text-sm font-semibold focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
            >
              {index+1}
            </div>
            )}
            <button
              onClick={e=>handleNext()}
              disabled={page*RESULT_PER_PAGE>=totalItems}
              className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </button>
          </nav>
        </div>
      </div>
    </div>
  );
}

export default AdminOrders;
