import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  addToCart,
  fetchCartItems,
  updateCartItem,
  deleteCartItem,
  deleteAllCartItem,
} from "./cartAPI";

const initialState = {
  status: "idle",
  cartItems: [],
  checkedCartItems:false
};

export const addToCartAsync = createAsyncThunk(
  "cart/addToCart",
  async (item) => {
    const response = await addToCart(item);
    return response.data;
  }
);

export const fetchCartItemsAsync = createAsyncThunk(
  "cart/fetchCartItems",
  async () => {
    const response = await fetchCartItems();
    return response.data;
  }
);

export const updateCartItemAsync = createAsyncThunk(
  "cart/updateCartItem",
  async (item) => {
    const response = await updateCartItem(item);
    return response.data;
  }
);

export const deleteCartItemAsync = createAsyncThunk(
  "cart/deleteCartItem",
  async (id) => {
    const response = await deleteCartItem(id);
    return response;
  }
);

export const deleteAllCartItemAsync = createAsyncThunk(
  "cart/deleteAllCartItem",
  async (userId) => {
    const response = await fetchCartItems(userId);
    let items = response.data;
    for (let key in items) {
      await deleteAllCartItem(items[key].id);
    }
    return { status: "success" };
  }
);

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    cartItemsToNull: (state) => {
      state.cartItems = [];
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addToCartAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems.push(action.payload);
      })
      .addCase(fetchCartItemsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchCartItemsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems = action.payload;
        state.checkedCartItems = true
      })
      .addCase(updateCartItemAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(updateCartItemAsync.fulfilled, (state, action) => {
        state.status = "idle";
        let index = state.cartItems.findIndex(
          (i) => i.id === action.payload.id
        );
        state.cartItems[index] = action.payload;
      })
      .addCase(deleteCartItemAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(deleteCartItemAsync.fulfilled, (state, action) => {
        state.status = "idle";
        let index = state.cartItems.findIndex((i) => i.id === action.payload);
        state.cartItems.splice(index, 1);
      })
      .addCase(deleteAllCartItemAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(deleteAllCartItemAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems = [];
      });
  },
});

export const { cartItemsToNull } = cartSlice.actions;

export const selectCartItems = (state) => state.cart.cartItems;
export const selectCheckedCartItems = (state) => state.cart.checkedCartItems;

export default cartSlice.reducer;
