import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addUserAddress, getUserAddress, getUser, editUserAddress } from './UserAPI';

const initialState = {
  value: 0,
  status: 'idle',
  addresses:null,
  user:null
};

export const addUserAddressAsync = createAsyncThunk(
  'user/addUserAddress',
  async (data) => {
    const response = await addUserAddress(data);
    return response;
  }
);
 
export const getUserAddressAsync = createAsyncThunk(
  'user/getUserAddress',
  async () => {
    const response = await getUserAddress();
    return response;
  }
);
 
export const getUserAsync = createAsyncThunk(
  'user/getUser',
  async () => {
    const response = await getUser();
    return response;
  }
);
 
export const editUserAddressAsync = createAsyncThunk(
  'user/editUserAddress',
  async (userInfo) => {
    const response = await editUserAddress(userInfo);
    return response;
  }
);
 
export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    userAddressesClear: (state) => {
      state.addresses = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addUserAddressAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addUserAddressAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.addresses = action.payload.addresses;
      })
      .addCase(getUserAddressAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(getUserAddressAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.addresses = action.payload.addresses;
      })
      .addCase(getUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(getUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
      })
      .addCase(editUserAddressAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(editUserAddressAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
        state.addresses = action.payload.addresses;
      })
  },
});

export const { userAddressesClear } = userSlice.actions;

export const selectUserAddresses = (state) => state.user.addresses
export const selectUserInfo = (state) => state.user.user
export const selectUserOrdersStatus = (state) => state.user.status

export default userSlice.reducer;
