import axios from "axios";
// A mock function to mimic making an async request for data
export function fetchAllOrders(ordersPagination) {
  let pageFilter = ""
  for(let key in ordersPagination){
    pageFilter += `${key}=${ordersPagination[key]}&`
  }
  return new Promise(async (resolve) => {
    try{
      const response = await axios.get("/order/admin/orders?"+pageFilter);
      const totalOrders = await response.headers.get('X-Total-Count')
      const ordersData = await response.data 
      resolve({totalOrders, ordersData});
    }catch(error){
      console.log(error)
    }
  });
}
 
export function updateOrder({orderId, status}) {
  return new Promise(async (resolve) => {
    try{
    const response = await axios.patch(`/order`, {id:orderId, status});
    resolve(response.data);
    }catch(error){
      console.log(error)
    }
  });
}
 
export function fetchOrderById(orderId) {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.get(`/orders/`+orderId);
      resolve(response.data);
    }catch(error){
      console.log(error)
    }
  });
}
 
export function deleteOrderById(orderId) {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.delete(`/order`, {data: {id:orderId}});
      resolve(orderId);
    }catch(error){
      console.log(error)
    }
  });
}