import axios from "axios";
export function fetchProductById(id) {
  return new Promise(async (resolve) =>{
    try{
      const response = await axios.get("/products/"+id)
      resolve(response.data)
    }catch(error){
      console.log(error)
    }
  }
  );
}

export function fetchAllProductsByFilter({filter, role}) {
  let newFilter = "";
  for (let key in filter) {
    let filterArray = filter[key];
    if (!(key === "sort" || key === "order")) {
      if (filterArray.length > 0) {
        for (let i = 0; i < filterArray.length; i++) {
          newFilter += `${key}=${filterArray[i]}&`;
        }
      } 
      else{
        if(filterArray.length === undefined){
          newFilter += `${key}=${filter[key]}&`;
        }
      }
    } 
    // sorting part
    else {
        newFilter += `${key}=${filter[key]}&`;
    }
  }
  return new Promise(async (resolve) => {
    try{
      const response = await axios.get(
        "/products?" + newFilter, {
          headers:{
            // TODO: Products will fetch and filter by role if role will be user then deleted item will not come in the response else user role will admin then all products come in the response with deleted true flag 
            role
          }
        }
      );
      const totalResult = await response.headers.get('X-Total-Count')
      const data = await response.data
      resolve({data, totalResult});
    }catch(error){
      console.log(error)
    }
  });
}

