export function adminAddProduct(product) {
  return new Promise(async (resolve) =>{
    try{
      const response = await axios.post(`/products`, product)
      resolve(response.data)
    }catch(error){
      console.log(error)
    }
  }
  );
}
 
export function adminEditProductById(product) {
  return new Promise(async (resolve) =>{
    try{
      const response = await axios.patch(`/products`, product)
      resolve(response.data)
    }catch(error){
      console.log(error)
    }
  }
  );
}
 
export function adminDeleteProductById(id) {
  return new Promise(async (resolve) =>{
    try{
      const response = await axios.delete(`/products`, {data: {id}})
      resolve(id)
    }catch(error){
      console.log(error)
    }
  }
  );
}

 