import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllProducts, fetchAllProductsByFilter, fetchAllCategories, fetchAllBrands, fetchAllSize,fetchProductById, adminAddProduct, adminEditProductById, adminDeleteProductById } from './ProductListAPI';

const initialState = {
  products: [],
  categories:[],
  brands:[],
  size:[],
  selectedProduct:null,
  status: 'idle',
  totalItems:0,
  continueShopping: false
};

export const fetchProductByIdAsync = createAsyncThunk(
  'product/fetchProductById',
  async (id) => {
    const response = await fetchProductById(id);
    return response;
  }
);

export const fetchAllProductsByFilterAsync = createAsyncThunk(
  'product/fetchAllProductsByFilter',
  async ({filter, role}) => {
    const response = await fetchAllProductsByFilter({filter, role});
    return response;
  }
);

export const fetchAllCategoriesAsync = createAsyncThunk(
  'product/fetchAllCategories',
  async () => {
    const response = await fetchAllCategories();
    return response;
  }
);

export const fetchAllBrandsAsync = createAsyncThunk(
  'product/fetchAllBrands',
  async () => {
    const response = await fetchAllBrands();
    return response;
  }
);
 
export const fetchAllSizeAsync = createAsyncThunk(
  'product/fetchAllSize',
  async () => {
    const response = await fetchAllSize();
    return response;
  }
);
 
export const adminAddProductAsync = createAsyncThunk(
  'product/adminAddProduct',
  async (product) => {
    const response = await adminAddProduct(product);
    return response;
  }
);
 
export const adminEditProductByIdAsync = createAsyncThunk(
  'product/adminEditProductById',
  async (product) => {
    const response = await adminEditProductById(product)
    return response;
  }
);

export const adminDeleteProductByIdAsync = createAsyncThunk(
  'product/adminDeleteProductById',
  async (id) => {
    const response = await adminDeleteProductById(id);
    return response;
  }
);
 
export const productSlice = createSlice({
  name: 'product',
  initialState,
  reducers: {
    continueShoppingStatus: (state) => {
      state.continueShopping = true
    },
    pauseShoppingStatus: (state) => {
      state.continueShopping = false
    },
  },
  extraReducers: (builder) => {
    builder
      // .addCase(fetchAllProductsAsync.pending, (state) => {
      //   state.status = 'loading';
      // })
      // .addCase(fetchAllProductsAsync.fulfilled, (state, action) => {
      //   state.status = 'idle';
      //   state.products = action.payload;
      // })
      .addCase(fetchAllProductsByFilterAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsByFilterAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products = action.payload.data;
        state.totalItems = action.payload.totalResult;
      })
      .addCase(fetchAllCategoriesAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllCategoriesAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.categories = action.payload;
      })
      .addCase(fetchAllBrandsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllBrandsAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.brands = action.payload;
      })
      .addCase(fetchAllSizeAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllSizeAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.size = action.payload;
      })
      .addCase(fetchProductByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.selectedProduct = action.payload;
      })
      .addCase(adminAddProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(adminAddProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products.push(action.payload);
      })
      .addCase(adminEditProductByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(adminEditProductByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        let index = state.products.findIndex((i) => i.id === action.payload.id);
        state.products.splice(index, 1, action.payload);        
      })
      .addCase(adminDeleteProductByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(adminDeleteProductByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        let index = state.products.findIndex((i) => i.id === action.payload);
        state.products.splice(index, 1)
      })
  },
});

export const { continueShoppingStatus, pauseShoppingStatus } = productSlice.actions;

export const selectAllProducts = (state) => state.product.products;
export const selectAllCategories = (state) => state.product.categories;
export const selectAllBrands = (state) => state.product.brands;
export const selectAllSize = (state) => state.product.size;
export const selectTotalItems = (state) => state.product.totalItems;
export const selectedProductById = (state) => state.product.selectedProduct;
export const selectShoppingStatus = (state) => state.product.continueShopping;
export const selectProductListStatus = (state) => state.product.status;

export default productSlice.reducer;