import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { loggedInUserClear } from "../features/auth/authSlice";
import { userAddressesClear } from "../features/user/UserSlice";
import { clearUserOrders } from "../features/order/orderSlice";

const LogoutPage = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(userAddressesClear())
    dispatch(clearUserOrders())
    setTimeout(() => {
      dispatch(loggedInUserClear());
    }, 1000);
  }, [dispatch]);
  return (
    <div>
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            We are trying to logout your account please wait......
          </h1>
          <p className="mt-6 text-base leading-7 text-gray-600">
            You will automatically redirect to login page.
          </p>
        </div>
      </main>
    </div>
  );
};

export default LogoutPage;
