import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectLoggedInUser } from "../../auth/authSlice";
import { fetchUserOrdersAsync, selectUserOrders } from "../../order/orderSlice";
import Loader from "../../comman/Loader";
import { selectUserOrdersStatus } from "../UserSlice";

export default function UserOrders() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const userOrders = useSelector(selectUserOrders);
  const userOrdersStatus = useSelector(selectUserOrdersStatus)

  useEffect(() => {
    dispatch(fetchUserOrdersAsync(user.id));
  }, []);
  return (
    <div>
      {userOrdersStatus === "loading"? <Loader/> : userOrders && userOrders.map((order, index) => { 
          return <div key={index} className="mx-auto bg-white mt-12 max-w-7xl px-2 sm:px-6 lg:px-8">
              <h1 className="text-4xl pt-10 font-bold tracking-tight text-gray-900">
                Order Id: #{order.id}
              </h1>
              <div className="mt-8 mb-6">
                <div className="flow-root">
                  <ul role="list" className="-my-6 divide-y divide-gray-200">
                    <li className="flex py-6">
                      <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                        <img
                          src={order.item.thumbnail}
                          alt={order.item.title}
                          className="h-full w-full object-cover object-center"
                        />
                      </div>

                      <div className="ml-4 flex flex-1 flex-col">
                        <div>
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            <div>
                              <h3>
                                <p>{order.item.title}</p>
                              </h3>
                              <p className="text-sm text-gray-500">
                                {order.item.brand}
                              </p>
                            </div>
                            <p className="ml-4">${order.item.price}</p>
                          </div>
                          <p className="mt-1 text-sm text-gray-500">
                            {order.color}
                          </p>
                        </div>
                        <div className="flex flex-1 items-end justify-between text-sm">
                          <div className="text-gray-500">
                            <label
                              htmlFor="quantity"
                              className="inline mr-2 text-sm font-medium leading-6 text-gray-900"
                            >
                              Qty
                            </label>
                            {order.quantity}
                          </div>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                <div className="flex justify-between text-base font-medium text-gray-900">
                  <p>Subtotal</p>
                  <p>${order.totalAmount}</p>
                </div>
                <div className="flex justify-between text-base font-medium text-gray-900">
                  <p>Total Items</p>
                  <p>{order.quantity}</p>
                </div>
                <p className="text-gray-900 mb-4 font-medium">Shipping Address</p>
                <div>
                  <li
                    key={order.address.email}
                    className="flex px-2 py-2 mb-2 border-2  border-gray-500 justify-between gap-x-6"
                  >
                    <div className="flex min-w-0 gap-x-4">
                      <div className="min-w-0 flex-auto">
                        <p className="text-sm font-semibold leading-6 text-gray-900">
                          {order.address.name}
                        </p>
                        <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                          {order.address.street}
                        </p>
                        <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                          {order.address.postalCode}
                        </p>
                      </div>
                    </div>
                    <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                      <p className="text-sm font-semibold leading-6 text-gray-900">
                        {order.address.telephone}
                      </p>
                      <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                        {order.address.city}
                      </p>
                    </div>
                  </li>
                </div>
              </div>

            </div>
        })}
    </div>
  );
}
