import axios from "axios";
// A mock function to mimic making an async request for data
export function addToCart(item) {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.post("/cart", item);
      resolve(response);
    }catch(error){
      console.log(error)
    }
  });
}

export function fetchCartItems() {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.get("/cart");
      resolve(response);
    }catch(error){
      console.log(error)
    }
  });
}
 
export function updateCartItem(item) {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.patch(`/cart`, item);
      // console.log(response.data)
      resolve(response);
    }catch(error){
      console.log(error)
    }
  });
}
 
export function deleteCartItem(id) {
  return new Promise(async (resolve) => {
    try{
      await axios.delete(`/cart`, {data: {id}});
      resolve(id);
    }catch(error){
      console.log(error)
    }
  });
}

export function deleteAllCartItem(id) {
  return new Promise(async (resolve) => {
    try{
      const response = await axios.delete(`/cart`, {data: {id}});
      resolve(response);
    }catch(error){
      console.log(error)
    }
  })
}

 