import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectCurrentOrders } from "../features/checkout/checkOutSlice";
import { selectLoggedInUser } from "../features/auth/authSlice";
import {
  deleteAllCartItemAsync,
} from "../features/cart/cartSlice";
import { Navigate } from "react-router-dom";
import { continueShoppingStatus, selectShoppingStatus } from "../features/product-list/ProductListSlice";

const PlacedOrderStatus = () => {
  const orders = JSON.parse(localStorage.getItem('currentOrders'))
  const shopping = useSelector(selectShoppingStatus)
  const user = useSelector(selectLoggedInUser)
  const dispatch = useDispatch();

  const handleContinueShopping = () => {
    dispatch(deleteAllCartItemAsync())
    dispatch(continueShoppingStatus())
    localStorage.removeItem("currentOrders")
  };
  return (
    <>
      { shopping && <Navigate to="/" replace={true} />}
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-base font-semibold text-indigo-600">
            Hurray! Your order has been placed successfully.
          </p>
          
          {orders?.map((el, i)=>{return <h2 key={i} className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            Your <span className="text-red-500" >{i+1}</span> Order Id #<span className="text-green-700" >{el.id}</span>
          </h2>}) }
          <p className="mt-6 text-base leading-7 text-gray-600">
            Your items placed successfully, Hope you enjoyed shopping experience
          </p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <button
              onClick={() => handleContinueShopping()}
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Continue Shopping
            </button>
            <a href="#" className="text-sm font-semibold text-gray-900">
              Contact support <span aria-hidden="true">&rarr;</span>
            </a>
          </div>
        </div>
      </main>
    </>
  );
};

export default PlacedOrderStatus;
