import React, { useEffect, useState, Fragment, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  editUserAddressAsync,
  getUserAddressAsync,
  getUserAsync,
  selectUserAddresses,
  selectUserInfo,
  userAddressesClear,
} from "../UserSlice";
import { selectLoggedInUser } from "../../auth/authSlice";
import { Dialog, Transition } from "@headlessui/react";
import { useForm } from "react-hook-form";
import { Navigate } from "react-router-dom";
import WarnModal from "../../comman/WarnModal";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const UserProfile = () => {
  const userInfo = useSelector(selectUserInfo);
  const addresses = useSelector(selectUserAddresses);
  const [selected, setSelected] = useState(null);
  const [formIndex, setFormIndex] = useState(null);
  const [ordersPage, setOrdersPage] = useState(false);
  const [itemId, setItemId] = useState(null);
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm();

  const cancelButtonRef = useRef(null);

  const handleEditModal = (address, index) => {
    setFormIndex(index);
    setValue("name", address.name);
    setValue("email", address.email);
    setValue("street", address.street);
    setValue("city", address.city);
    setValue("state", address.state);
    setValue("telephone", address.telephone);
    setValue("postalCode", address.postalCode);
  };

  const handleEdit = (data) => {
    let modifiedAddress = addresses? [...addresses] : []
    if (formIndex!==null) {
      modifiedAddress.splice(formIndex, 1, { ...data });
    } else {
      modifiedAddress.push(data);
    }
    dispatch(editUserAddressAsync({ ...userInfo, addresses: modifiedAddress }));
    setOpen(false);
    reset()
    setFormIndex(null);
  };

  const handleDelete = (index) => {
    setDeleteOpen(true);
    setItemId(index);
  };

  const handleDeleteConfirm = () => {
    let modifiedAddress = [...addresses];
    modifiedAddress.splice(itemId, 1);
    dispatch(editUserAddressAsync({ ...userInfo, addresses: modifiedAddress }));
    reset();
  };

  useEffect(() => {
    dispatch(getUserAsync());
    dispatch(userAddressesClear());
  }, [dispatch]);
  return (
    <>
      <WarnModal
        open={deleteOpen}
        setOpen={setDeleteOpen}
        handleDeleteConfirm={handleDeleteConfirm}
        currentItem={{ title: "Address", object: "address" }}
      />
      {ordersPage && <Navigate to="/orders" replace={true} />}
      {userInfo && (
        <div>
          <div className="rounded-xl flex items-center justify-between mx-auto bg-white mt-12 max-w-7xl px-2 sm:px-6 lg:px-8">
            <div className="flex items-center">
              <img
                className="rounded-full mt-2 mb-2"
                src={userInfo.userImage}
                alt={userInfo.name}
                width="150px"
              />
              <div className="flex-col ml-4 mb-4">
                <h1 className="text-3xl">{userInfo.name}</h1>
                <p>{userInfo.email}</p>
                {userInfo.role === "admin" && (
                  <strong>
                    <p className="mt-1 text-red-500">Role: {userInfo.role}</p>
                  </strong>
                )}
              </div>
            </div>
            <div className="mt-1 flex items-center gap-x-1.5">
              <div className="flex-none rounded-full bg-emerald-500/20 p-1">
                <div className="h-1.5 w-1.5 rounded-full bg-emerald-500"></div>
              </div>
              <p className="text-lg leading-5 text-gray-500">Online</p>
            </div>
          </div>

          {/* address part */}
          <div
            className={` transition ease-in-out delay-150 rounded-lg mx-auto mt-8 max-w-7xl ${
              addresses !== null ? "border-2 border-blue-600" : ""
            }`}
          >
            <div className="rounded-lg mx-auto bg-white max-w-7xl px-2 py-1 text-xl cursor-pointer text-black">
              <div className="flex justify-between py-1">
                <span
                  onClick={() => {
                    addresses === null
                      ? dispatch(getUserAddressAsync())
                      : dispatch(userAddressesClear());
                  }}
                  className="rounded-lg z-10 bg-white max-w-7xl px-3 py-1 text-xl cursor-pointer text-black"
                >
                  <h1>
                    <b>Addresses</b>
                  </h1>
                </span>
                <button
                  onClick={() => {
                    setOpen(true);
                    console.log(open);
                  }}
                  className="rounded-lg bg-white border-2 border-blue-900 max-w-7xl px-3 py-1 text-xl cursor-pointer text-black"
                >
                  <h1>
                    <b>Add Address</b>
                  </h1>
                </button>
              </div>
            </div>
            {addresses !== null &&
              addresses.map((address, index) => {
                return (
                  <div
                    key={index}
                    className="rounded-lg py-2 flex flex-col  justify-between mx-auto bg-white mt-2 max-w-7xl px-2 sm:px-6 lg:px-8"
                  >
                    <div className="pr-4 transition ease-in-out delay-150">
                      <div
                        className={classNames(
                          selected ? "font-semibold" : "font-normal",
                          "ml-3 block truncate"
                        )}
                      >
                        <div className="flex justify-between">
                          <span>
                            <b>Name:</b>
                          </span>
                          {address.name}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>Email:</b>
                          </span>
                          {address.email}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>Street:</b>
                          </span>
                          {address.street}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>City:</b>
                          </span>
                          {address.city}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>State:</b>
                          </span>
                          {address.state}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>Phone:</b>
                          </span>
                          {address.telephone}
                        </div>
                        <div className="flex justify-between">
                          <span>
                            <b>Pincode:</b>
                          </span>
                          {address.postalCode}
                        </div>
                      </div>
                      <button
                        className="bg-red-600 text-white py-1 px-4 ml-3 mt-2"
                        onClick={() => {
                          setOpen(true);
                          handleEditModal(address, index);
                        }}
                      >
                        Edit
                      </button>
                      <button
                        className="bg-red-600 text-white py-1 px-4 ml-3 mt-2"
                        onClick={() => {
                          setDeleteOpen(true);
                          handleDelete(index);
                        }}
                      >
                        Delete
                      </button>
                    </div>
                  </div>
                );
              })}
          </div>

          {/* <!-- Main modal --> */}
          <Transition.Root show={open} as={Fragment}>
            <Dialog
              as="div"
              className="relative z-10"
              initialFocus={cancelButtonRef}
              onClose={() => setOpen(true)}
            >
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
              </Transition.Child>

              <div className="fixed inset-0 z-10 overflow-y-auto">
                <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                  <Transition.Child
                    as={Fragment}
                    enter="ease-out duration-300"
                    enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                    enterTo="opacity-100 translate-y-0 sm:scale-100"
                    leave="ease-in duration-200"
                    leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                    leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                  >
                    <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">
                      <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4">
                        <div className="sm:flex sm:items-start">
                          <div className="mt-3 text-center sm:ml-4 sm:mt-0 sm:text-left">
                            <Dialog.Title
                              as="h3"
                              className="text-base font-semibold leading-6 text-gray-900"
                            >
                              Edit Existing Address
                            </Dialog.Title>
                            <div className="mt-0">
                              {/* form area */}
                              {editForm({
                                handleSubmit,
                                handleEdit,
                                formIndex,
                                reset,
                                setFormIndex,
                                register,
                                setOpen,
                                cancelButtonRef,
                              })}
                            </div>
                          </div>
                        </div>
                      </div>
                    </Dialog.Panel>
                  </Transition.Child>
                </div>
              </div>
            </Dialog>
          </Transition.Root>

          {/* orders part */}
          <div
            className={` transition ease-in-out delay-150 rounded-lg mx-auto mt-4 max-w-7xl`}
          >
            <div onClick={() => setOrdersPage(true)}>
              <h1 className="rounded-lg flex flex-col justify-between mx-auto bg-white max-w-7xl px-2 py-2 text-xl cursor-pointer text-black">
                <b>My Orders</b>
              </h1>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

function editForm({
  handleSubmit,
  formIndex,
  setFormIndex,
  handleEdit,
  register,
  reset,
  setOpen,
  cancelButtonRef,
}) {
  return (
    <form
      noValidate
      className="mx-auto bg-white max-w-7xl py-4 px-0 sm:px-0 lg:px-0"
      onSubmit={handleSubmit((data, e) => {
        e.preventDefault();
        handleEdit(data);
      })}
    >
      <div className="space-y-12">
        <div className="border-b  border-gray-900/10 pb-12">
          <div className="mt-0 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-3">
              <label
                htmlFor="first-name"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Full Name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="fullname"
                  id="fullname"
                  {...register("name", {
                    required: "Name is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-4">
              <label
                htmlFor="email"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Email address
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  name="email"
                  type="email"
                  {...register("email", {
                    required: "Email is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="col-span-full">
              <label
                htmlFor="street-address"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Street address
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="street"
                  id="street"
                  {...register("street", {
                    required: "Street is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2 sm:col-start-1">
              <label
                htmlFor="city"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                City
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="city"
                  id="city"
                  {...register("city", {
                    required: "City is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="state"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                State / Province
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="state"
                  id="region"
                  {...register("state", {
                    required: "State is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="telephone"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Phone No.
              </label>
              <div className="mt-2">
                <input
                  type="tel"
                  name="telephone"
                  id="telephone"
                  {...register("telephone", {
                    required: "Phone Number is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="postalCode"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                ZIP / Postal code
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="postalCode"
                  id="postalCode"
                  {...register("postalCode", {
                    required: "postalCode is required",
                  })}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
        <button
          type="submit"
          className="inline-flex w-full justify-center rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 sm:ml-3 sm:w-auto"
        >
          {formIndex? "Edit" : "Add Address"}
        </button>
        <button
          type="button"
          className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto"
          onClick={() => {
            setOpen(false)
            setFormIndex(null)
            reset()
          }}
          ref={cancelButtonRef}
        >
          Cancel
        </button>
      </div>
    </form>
  );
}

export default UserProfile;
