import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { placeOrder } from "./checkOutAPI";

const initialState = {
  status: "idle",
  placeOrderStatus: false,
  currentOrders:null
};

export const placeOrderAsync = createAsyncThunk(
  "cart/placeOrder",
  async ({ cartItems, shippingAddress, paymentMode }) => {
    const response = []
    for (let i = 0; i < cartItems.length; i++) {
      const { product, quantity } = cartItems[i];
      const order = {item: product.id, quantity: quantity, totalAmount: cartItems[i].product.price * quantity, address: shippingAddress, paymentMethod: paymentMode, };
      const createdOrder = await placeOrder(order);
      response.push(createdOrder)
    }
    return {response,paymentMode};
  }
);

export const checkOutSlice = createSlice({
  name: "checkOut",
  initialState,
  reducers: {
    changePlaceOrderStatus: (state) => {
      state.placeOrderStatus = false;
      state.currentOrders = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(placeOrderAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(placeOrderAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.placeOrderStatus = true
        state.currentOrders = action.payload.response
        localStorage.setItem("currentOrders", JSON.stringify(state.currentOrders))
      });
  },
});

export const { changePlaceOrderStatus } = checkOutSlice.actions;

export const selectPlaceOrderStatus = (state) => state.checkOut.placeOrderStatus;
export const selectCurrentOrders = (state) => state.checkOut.currentOrders;

export default checkOutSlice.reducer;
