import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";

import CheckoutForm from "./CheckoutForm";
import "./Stripe.css";
import { useSelector } from "react-redux";
import { selectCurrentOrders } from "../checkout/checkOutSlice";

// Make sure to call loadStripe outside of a components render to avoid
// recreating the Stripe object on every render.
// This is a public sample test API key.
// Dont submit any personally identifiable information in requests made with this key.
// Sign in to see your own test API key embedded in code samples.
const stripePromise = loadStripe("pk_test_51NlamKSBiL0iAeKcLkz5dmgXsvxlyOZcHYYUxcKoL6c9Mfs4KeVcNbrfKP6XHQRh2ncbERFdei2YXNcQk4xaReqv00CtmZHxAG");

export default function Stripe() {
  const [clientSecret, setClientSecret] = useState("");
  const currentOrders = useSelector(selectCurrentOrders)

  useEffect(() => {
    // Create PaymentIntent as soon as the page loads
    const totalAmount = currentOrders.reduce((amount, item)=>item.totalAmount + amount, 0)
    fetch("/create-payment-intent", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ totalAmount }),
      metadata:{
        order_id: currentOrders[0].id
      }
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, []);

  const appearance = {
    theme: 'stripe',
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
    <div className="Stripe">
      {clientSecret && (
        <Elements options={options} stripe={stripePromise}>
          <CheckoutForm />
        </Elements>
      )}
    </div>
  );
}